.header {
  width: 100%;
  height: 70px;
  line-height: 70px;
}
.nav_container {
  display: flex;
  align-items: center;
  justify-content: space-between;
} 
.logo {
  
  display: flex;
  align-items: center;
  column-gap: 0.5rem;
}
.logo-img {
  width: 40px;
  height: 40px;
  padding: 10px;
  line-height: 40px;
  border-radius: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.logo-img img{
    width: 6rem;
}
.navigation{
    display: flex;
    justify-content: space-between;
}
.menu{
  display: flex;
  align-items: center;
  column-gap: 2.5rem;

}
.nav_item{
  color: var(--heading-color);
  font-weight: 600 ;
  cursor: pointer;
  transition: 0.3s;
}
.nav_item:hover{
  color: var(--primary-color);
}